html {
  font-size: 92%;
  box-sizing: border-box;
}

body {
  margin: 0%;
  font:  helvetica;
  height: 100vh;
}

.App {
  display: grid;
  width: 100%;
  flex-wrap: wrap;
}

.Main {
  display: flex;
  justify-content: space-between;
  margin: 12px 12px;
}

.Navbar_Area {
  width: 20%;
  display: grid;
  height: 40rem;
}

.Main_Area {
  width: 50%;
  display: grid;
  height: 40rem;
  background-color: coral;
}

.Left_Navbar_Area {
  width: 30%;
  height: 40rem;
  display: grid;
  background-color: blue;
}